:root {
  --dark-1: #0f172a;
  --dark-2: #1e293b;
  --dark-3: #334155;
  --light-1: #f8fafc;
  --light-2: #e2e8f0;
  --light-3: #94a3b8;
  --primary: #3b82f6;
  --secondary: #6366f1;
}

/* ===== Base Layout ===== */
.homepage {
  flex-grow: 1;
  padding: clamp(1rem, 3vw, 2rem);
  background: linear-gradient(180deg, var(--dark-1) 0%, var(--dark-2) 100%);
  overflow-y: auto;
  min-height: 100vh;
  position: relative;
  z-index: 0;
}

.homepage::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    radial-gradient(circle at 20% 30%, rgba(59, 130, 246, 0.15) 0%, transparent 40%),
    radial-gradient(circle at 80% 70%, rgba(99, 102, 241, 0.1) 0%, transparent 40%);
  pointer-events: none;
  z-index: -1;
}

.section-container {
  max-width: min(1200px, 90vw);
  margin: 0 auto;
  animation: fadeIn 0.6s ease-out forwards;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

.section-title {
  color: var(--light-1);
  margin: clamp(1rem, 3vw, 1.5rem) 0;
  font-size: clamp(1.8rem, 5vw, 2.4rem);
  font-weight: 700;
  position: relative;
  display: inline-block;
  padding-bottom: 0.5rem;
}

.section-title::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: min(80px, 20vw);
  height: clamp(3px, 0.5vw, 4px);
  background: linear-gradient(to right, var(--primary), var(--secondary));
  border-radius: 4px;
}

.section-content {
  background: linear-gradient(145deg, var(--dark-2) 0%, var(--dark-3) 100%);
  padding: clamp(1.5rem, 3vw, 2rem);
  border-radius: clamp(8px, 2vw, 16px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(8px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  margin-bottom: clamp(1.5rem, 3vw, 2rem);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.section-content:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.4);
}

/* ===== Experience Timeline ===== */
.experience-timeline {
  position: relative;
  padding-left: clamp(1rem, 3vw, 1.5rem);
}

.experience-timeline::before {
  content: '';
  position: absolute;
  left: calc(0.5rem + 0.5vw);
  top: 0;
  height: 100%;
  width: clamp(2px, 0.5vw, 3px);
  background: linear-gradient(to bottom, var(--primary), var(--secondary));
}

.experience-item {
  position: relative;
  margin-bottom: clamp(1.5rem, 3vw, 2rem);
  padding-left: clamp(1rem, 3vw, 1.5rem);
  transition: all 0.3s ease;
}

.experience-item:hover {
  transform: translateX(5px);
}

.experience-item::before {
  content: '';
  position: absolute;
  left: calc(-0.5rem - 0.5vw);
  top: 0.5rem;
  width: clamp(0.8rem, 2vw, 1rem);
  height: clamp(0.8rem, 2vw, 1rem);
  border-radius: 50%;
  background: var(--primary);
  border: 3px solid var(--dark-2);
  transition: all 0.3s ease;
}

.experience-item:hover::before {
  transform: scale(1.1);
  box-shadow: 0 0 10px rgba(59, 130, 246, 0.5);
}

.company {
  color: var(--light-1);
  font-weight: 600;
  font-size: clamp(1rem, 2vw, 1.2rem);
  margin: 0.25rem 0;
}

.duration, .grade {
  color: var(--light-3);
  font-size: clamp(0.8rem, 1.5vw, 0.9rem);
  margin-bottom: clamp(0.5rem, 1.5vw, 0.75rem);
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.duration::before {
  content: '📅';
  font-size: 0.8em;
}

.grade::before {
  content: '🏆';
}

.achievements {
  list-style-type: none;
  padding-left: 0;
}

.achievements li {
  margin-bottom: clamp(0.5rem, 1.5vw, 0.75rem);
  padding-left: clamp(1rem, 3vw, 1.5rem);
  position: relative;
  line-height: 1.6;
}

.achievements li::before {
  content: '▹';
  position: absolute;
  left: 0;
  color: var(--primary);
  font-size: 1.2em;
}

/* ===== Skills Grid ===== */
.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(min(250px, 100%), 1fr));
  gap: clamp(1rem, 2vw, 1.5rem);
}

.skill-category {
  transition: transform 0.3s ease;
}

.skill-category:hover {
  transform: translateY(-3px);
}

.skill-category h3 {
  color: var(--light-1);
  margin-bottom: clamp(0.5rem, 1.5vw, 0.75rem);
  font-size: clamp(1rem, 2vw, 1.1rem);
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.skill-category h3::before {
  content: '🛠️';
  font-size: 0.9em;
}

.skill-category ul {
  list-style-type: none;
  padding-left: 0;
}

.skill-category li {
  margin-bottom: clamp(0.3rem, 1vw, 0.5rem);
  padding-left: clamp(1rem, 2.5vw, 1.25rem);
  position: relative;
  transition: color 0.2s ease;
}

.skill-category li:hover {
  color: var(--light-2);
}

.skill-category li::before {
  content: '•';
  position: absolute;
  left: 0;
  color: var(--primary);
  font-size: 1.5em;
  line-height: 0;
}

/* ===== Education ===== */
.education-list {
  display: flex;
  flex-direction: column;
  gap: clamp(1.5rem, 3vw, 2rem);
}

.education-item {
  background: rgba(30, 41, 59, 0.4);
  padding: clamp(1rem, 2vw, 1.5rem);
  border-radius: clamp(8px, 2vw, 12px);
  border-left: clamp(3px, 1vw, 4px) solid var(--primary);
  transition: all 0.3s ease;
}

.education-item:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  background: rgba(30, 41, 59, 0.6);
}

.institution {
  font-weight: 600;
  color: var(--light-1);
  font-size: clamp(1rem, 2vw, 1.2rem);
  margin: clamp(0.3rem, 1vw, 0.5rem) 0;
}

.skills {
  margin-top: clamp(0.8rem, 2vw, 1rem);
  padding-top: clamp(0.8rem, 2vw, 1rem);
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.skills h4 {
  font-size: clamp(0.9rem, 1.8vw, 1rem);
  margin-bottom: clamp(0.3rem, 1vw, 0.5rem);
  color: var(--light-2);
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.skills h4::before {
  content: '🧠';
}

.skills ul {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  gap: clamp(0.3rem, 1vw, 0.5rem);
}

.skills li {
  background: rgba(59, 130, 246, 0.2);
  padding: clamp(0.2rem, 1vw, 0.3rem) clamp(0.5rem, 2vw, 0.8rem);
  border-radius: 20px;
  font-size: clamp(0.75rem, 1.5vw, 0.85rem);
  transition: all 0.2s ease;
}

.skills li:hover {
  background: rgba(59, 130, 246, 0.3);
  transform: scale(1.05);
}

/* ===== Certifications ===== */
.certifications {
  margin-top: clamp(1.5rem, 3vw, 2rem);
}

.certifications h3 {
  margin-bottom: clamp(0.8rem, 2vw, 1rem);
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.certifications h3::before {
  content: '📜';
}

.certifications ul {
  list-style-type: none;
  padding-left: 0;
}

.certifications li {
  margin-bottom: clamp(0.5rem, 1.5vw, 0.75rem);
  padding-left: clamp(1rem, 3vw, 1.5rem);
  position: relative;
  transition: transform 0.2s ease;
}

.certifications li:hover {
  transform: translateX(5px);
}

.certifications li::before {
  content: '▹';
  position: absolute;
  left: 0;
  color: var(--primary);
  font-size: 1.2em;
}

/* ===== Cover Letter ===== */
.cover-letter {
  line-height: 1.8;
}

.letter-header h2 {
  margin-bottom: clamp(0.3rem, 1vw, 0.5rem);
}

.date {
  color: var(--light-3);
  margin-bottom: clamp(1rem, 2vw, 1.5rem);
  display: block;
}

.letter-content p {
  margin-bottom: clamp(0.8rem, 2vw, 1rem);
  text-align: justify;
}

.letter-closing {
  margin-top: clamp(1.5rem, 3vw, 2rem);
}

.signature {
  font-weight: 600;
  margin: clamp(0.3rem, 1vw, 0.5rem) 0;
}

/* ===== Responsive Adjustments ===== */
@media (max-width: 768px) {
  .skills-grid {
    grid-template-columns: 1fr;
  }

  .experience-timeline::before {
    left: 0.35rem;
  }

  .experience-item::before {
    left: -0.35rem;
  }
}

@media (max-width: 480px) {
  .homepage {
    padding: 1rem;
  }

  .section-content {
    padding: 1.2rem;
  }

  .achievements li,
  .certifications li {
    padding-left: 1.2rem;
  }
}
