/* ===== RESPONSIVE DESIGN ===== */
@media (max-width: 1024px) {
  .main-content {
    padding-left: 0;
  }

  .network-lines {
    background-image: none;
  }
}

@media (max-width: 768px) {
  .tech-circle {
    right: -30vw;
    bottom: -30vw;
  }
}

/* ===== PRINT STYLES ===== */
@media print {
  .sidebar-container,
  .mobile-header,
  .background-elements {
    display: none !important;
  }

  .main-content {
    padding-left: 0 !important;
  }

  body {
    background: white !important;
    color: black !important;
  }
}
