/* Sidebar Container */
.sidebar-container {
    position: relative;
    z-index: 20;
    transition: transform var(--transition-slow);
  }
  
  /* Sidebar */
  .sidebar {
    width: 100%;
    max-width: var(--sidebar-width);
    height: 100vh;
    background: linear-gradient(145deg, var(--dark-2) 0%, var(--dark-1) 100%);
    padding: clamp(1.5rem, 3vw, 2rem);
    position: fixed;
    left: 0;
    top: 0;
    z-index: 10;
    overflow-y: auto;
    border-right: 1px solid rgba(255, 255, 255, 0.08);
    transition: transform var(--transition-slow), box-shadow var(--transition-normal);
  }
  
  .sidebar:hover {
    box-shadow: var(--shadow-lg);
  }
  
  /* Sidebar Inner */
  .sidebar-inner {
    display: flex;
    flex-direction: column;
    height: 100%;
    gap: clamp(1.5rem, 3vw, 2rem);
  }
  
  /* Profile Card */
  .profile-card {
    background: linear-gradient(145deg, var(--dark-2) 0%, var(--dark-3) 100%);
    border-radius: clamp(12px, 2vw, 16px);
    padding: clamp(1rem, 2vw, 1.5rem);
    border: 1px solid rgba(255, 255, 255, 0.1);
    box-shadow: var(--shadow-sm);
    transition: transform var(--transition-normal);
  }
  
  .profile-card:hover {
    transform: translateY(-3px);
  }
  
  /* Avatar */
  .avatar-container {
    width: clamp(100px, 20vw, 120px);
    height: clamp(100px, 20vw, 120px);
    margin: 0 auto clamp(1rem, 2vw, 1.5rem);
    position: relative;
  }
  
  .avatar {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    overflow: hidden;
    position: relative;
    border: 3px solid rgba(255, 255, 255, 0.1);
    transition: all var(--transition-normal);
  }
  
  .avatar:hover {
    border-color: rgba(59, 130, 246, 0.5);
    transform: scale(1.05);
  }
  
  .avatar-face {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform var(--transition-normal);
  }
  
  .avatar:hover .avatar-face {
    transform: scale(1.1);
  }
  
  /* Profile Info */
  .profile-info h1 {
    font-size: clamp(1.3rem, 3vw, 1.5rem);
    text-align: center;
    margin-bottom: clamp(0.2rem, 0.5vw, 0.25rem);
    color: var(--light-1);
  }
  
  .title {
    text-align: center;
    color: var(--light-3);
    margin-bottom: clamp(0.8rem, 2vw, 1rem);
    font-size: clamp(0.9rem, 2vw, 1rem);
    position: relative;
  }
  
  .title::after {
    content: '';
    display: block;
    width: 50px;
    height: 2px;
    background: linear-gradient(to right, var(--primary), transparent);
    margin: 0.5rem auto 0;
  }
  
  /* Contact Info */
  .contact-info {
    display: flex;
    flex-direction: column;
    gap: clamp(0.5rem, 1.5vw, 0.75rem);
    margin: clamp(1rem, 2vw, 1.5rem) 0;
  }
  
  .contact-item {
    display: flex;
    align-items: center;
    gap: clamp(0.5rem, 1.5vw, 0.75rem);
    font-size: clamp(0.8rem, 1.8vw, 0.9rem);
    transition: transform var(--transition-fast);
  }
  
  .contact-item:hover {
    transform: translateX(5px);
  }
  
  .contact-icon {
    width: clamp(28px, 5vw, 30px);
    height: clamp(28px, 5vw, 30px);
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(255, 255, 255, 0.08);
    border-radius: 50%;
    transition: all var(--transition-normal);
  }
  
  .contact-item:hover .contact-icon {
    background: rgba(59, 130, 246, 0.2);
    transform: scale(1.1);
  }
  
  /* Quick Stats */
  .quick-stats {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: clamp(0.5rem, 1.5vw, 0.75rem);
    margin-top: clamp(0.8rem, 2vw, 1rem);
  }
  
  .stat-item {
    text-align: center;
    padding: 0.5rem;
    border-radius: 8px;
    transition: all var(--transition-normal);
  }
  
  .stat-item:hover {
    background: rgba(255, 255, 255, 0.05);
  }
  
  .stat-value {
    font-size: clamp(1rem, 2.5vw, 1.1rem);
    font-weight: 700;
    color: var(--primary);
    transition: all var(--transition-normal);
  }
  
  .stat-item:hover .stat-value {
    transform: scale(1.1);
  }
  
  .stat-label {
    font-size: clamp(0.65rem, 1.5vw, 0.7rem);
    opacity: 0.8;
  }
  
  /* Sidebar Navigation */
  .sidebar-nav {
    display: flex;
    flex-direction: column;
    gap: clamp(0.4rem, 1vw, 0.5rem);
  }
  
  .nav-item {
    padding: clamp(0.6rem, 1.5vw, 0.75rem) clamp(0.8rem, 2vw, 1rem);
    border-radius: clamp(6px, 1.5vw, 8px);
    display: flex;
    align-items: center;
    gap: clamp(0.5rem, 1.5vw, 0.75rem);
    background: rgba(30, 41, 59, 0.6);
    border: 1px solid rgba(255, 255, 255, 0.15);
    transition: all var(--transition-normal);
    color: var(--light-1);
    font-size: clamp(0.85rem, 2vw, 1rem);
  }
  
  .nav-item:hover {
    background: rgba(30, 41, 59, 0.8);
    transform: translateX(5px);
  }
  
  .nav-item.active {
    background: linear-gradient(90deg, rgba(59, 130, 246, 0.8), rgba(99, 102, 241, 0.7));
    color: white;
    box-shadow: 0 5px 15px rgba(59, 130, 246, 0.3);
  }
  
  /* Tech Stack */
  .tech-stack {
    margin-top: clamp(0.8rem, 2vw, 1rem);
  }
  
  .tech-stack h3 {
    margin-bottom: clamp(0.5rem, 1.5vw, 0.75rem);
    font-size: clamp(0.9rem, 2vw, 1rem);
    display: flex;
    align-items: center;
    gap: 0.5rem;
  }
  
  .tech-stack h3::before {
    content: '🧑‍💻';
  }
  
  .tech-tags {
    display: flex;
    flex-wrap: wrap;
    gap: clamp(0.3rem, 1vw, 0.5rem);
  }
  
  .tech-tag {
    padding: clamp(0.3rem, 1vw, 0.4rem) clamp(0.6rem, 1.5vw, 0.8rem);
    border-radius: 20px;
    font-size: clamp(0.75rem, 1.5vw, 0.8rem);
    background: rgba(255, 255, 255, 0.08);
    border: 1px solid rgba(255, 255, 255, 0.15);
    transition: all var(--transition-fast);
  }
  
  .tech-tag:hover {
    background: rgba(255, 255, 255, 0.15);
    transform: translateY(-2px);
  }
  
  .tech-tag.primary {
    background: rgba(59, 130, 246, 0.3);
  }
  
  .tech-tag.primary:hover {
    background: rgba(59, 130, 246, 0.4);
  }
  
  /* Mobile Header */
  .mobile-header {
    display: none;
    align-items: center;
    justify-content: space-between;
    padding: clamp(0.8rem, 2vw, 1rem);
    background: var(--dark-2);
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    z-index: 30;
    position: sticky;
    top: 0;
    backdrop-filter: blur(8px);
  }
  
  .menu-toggle {
    background: none;
    border: none;
    color: var(--light-2);
    font-size: clamp(1.2rem, 3vw, 1.5rem);
    cursor: pointer;
    transition: transform var(--transition-normal);
    padding: 0.5rem;
    border-radius: 50%;
  }
  
  .menu-toggle:hover {
    transform: scale(1.1);
    background: rgba(255, 255, 255, 0.1);
  }
  
  .mobile-title {
    font-size: clamp(1rem, 3vw, 1.2rem);
    font-weight: 600;
    margin-left: clamp(0.5rem, 2vw, 1rem);
    background: linear-gradient(90deg, var(--light-1), var(--light-3));
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
  }
  
  @media (max-width: 1024px) {
    .sidebar-container {
      position: fixed;
      top: 0;
      left: 0;
      height: 100vh;
      height: 100dvh;
      z-index: 25;
      box-shadow: var(--shadow-lg);
      transform: translateX(-100%);
    }
  
    .sidebar-container.active {
      transform: translateX(0);
    }
  
    .mobile-header {
      display: flex;
    }
  
    .main-content.active {
      transform: translateX(var(--sidebar-width));
    }
  }
  
  @media (max-width: 768px) {
    .sidebar-inner {
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center;
    }
    
    .profile-card {
      width: 100%;
    }
    
    .sidebar-nav {
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center;
    }
    
    .nav-item {
      flex: 1 0 calc(50% - 1rem);
      justify-content: center;
    }
  }
  
  @media (max-width: 480px) {
    .sidebar {
      padding: 1.2rem;
    }
    
    .sidebar-inner {
      gap: 1.2rem;
    }
    
    .nav-item {
      flex: 1 0 100%;
    }
    
    .tech-tags {
      justify-content: center;
    }
  }
